import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import React, { useState, useEffect } from "react";
import Typography from "@mui/material/Typography";
import { CardActionArea } from "@mui/material";
import { Box } from "@mui/system";
import useTheme from "@mui/material/styles/useTheme";
import Grid from "@mui/material/Grid";

function MindTraps({ formData, setFormData, setIsDone }) {
  const mindTrapsArray = [
    {
      name: "Allt eller inget tnkande",
      desc: "Alla hndelser delas in i ytterligheter, man har antingen lyckats eller misslyckats, man ser inte nyanser eller grskalor.",
      exampel: "Om jag inte r perfekt s r jag misslyckad",
      id: "0",
    },
    {
      name: "Mentalt filter (Selektiv abstraktion)",
      desc: "Nr man endast uppmrksammar vissa typer av bevis, man ser endast en neagtiv detalj istllet fr att se till helheten.",
      exampel: "Att enbart se sina misslyckande men inte sina framgngar",
      id: "1",
    },
    {
      name: "Tankelsning",
      desc: "Nr man frestller sig att man vet eller drar frhastade slutsatser om vad andra tnker.",
      exampel: "De sger ingenting, men jag frstr ju vad de tnker om mig.",
      id: "2",
    },
    {
      name: "Siarens misstag",
      desc: "Du siar om framtiden och r helt sker p att saker kommer g dligt. Du frutstter att det vrsta kommer hnda.",
      exampel: "Alla p festen kommer tycka jag r trkig",
      id: "3",
    },
    {
      name: "Emotionellt tnkande",
      desc: "Nr man utgr frn att bara fr att man knner p ett visst stt s mste det man tnker vara sant",
      exampel: "Jag knner mig dum allts r jag det",
      id: "4",
    },
    {
      name: "Etikettering",
      desc: "Att dma och stta etiketter p sig sjlv och andra mnniskor.",
      exampel: "Jag r en frlorare. De r idioter",
      id: "5",
    },
    {
      name: "vergeneralisering",
      desc: "Nr man tycker sig se ett mnster baserat p en enstaka hndelse eller drar frhastade slutsatser ver saker som har hnt. ",
      exampel: "Allting r alltid dligt. Det hnder aldrig ngot bra.",
      id: "6",
    },
    {
      name: "Diskvalificering",
      desc: "Nr man av ngon anledning rknar bort alla positiva saker som har hnt eller som man har gjort.",
      exampel:
        "Om jag lyckas bra med ngot s r det en lyckotrff, inte jag som gjort ngot bra.",
      id: "7",
    },
    {
      name: "Frstoring",
      desc: "D man frstorar upp saker utom rimliga proportioner.",
      exampel:
        "Jag gr ett misstag p jobbet och r drmed vertygad om att jag kommer bli avskedad.",
      id: "8",
    },
    {
      name: "Borden/Msten (Pliktpstenden)",
      desc: "Nr man har en bestmd uppfattning om hur man sjlv eller andra ska bete sig. Du stller krav och belastar dig med msten, borden, alltid, aldrig, ska osv.",
      exampel: "Man ska alltid vara andra till lags",
      id: "9",
    },
    {
      name: "Personalisering",
      desc: "Nr man klandrar sig sjlv eller tar ansvar fr ngot som inte var ens fel eller skyller p andra fr saker som man sjlv gjort fel. ",
      exampel:
        "Den andra personen blir tyst under ett samtal, det betyder skert att hen tycker vrt samtal r trkigt.",
      id: "10",
    },
  ];
  const [selected, setSelected] = React.useState(new Set());
  const userMindTraps = [];

  useEffect(() => {
    console.log(selected.size);

    selected.forEach((item, index) => {
      console.log("utem");
      console.log(item);
      if (item === 0) {
        userMindTraps.push(mindTrapsArray[item]);
      }
      if (item) {
        userMindTraps.push(mindTrapsArray[item]);
      }
    });
    console.log("userMindTraps");
    console.log(userMindTraps);
    setFormData({
      ...formData,
      tankeFallor: userMindTraps,
    });
    console.log("fromData");
    console.log(formData);
  }, [selected]);

  function handleSelectionChanged(id) {
    // treat state as immutable
    // React only does a shallow comparison so we need a new Set
    const newSet = new Set(selected);
    if (newSet.has(id)) newSet.delete(id);
    else newSet.add(id);
    setSelected(newSet);
  }
  const theme = useTheme();

  return (
    <>
      <Box
        sx={{
          textAlign: "left",
          width: "100%",
        }}
      >
        <Grid container justifyContent="center">
          {mindTrapsArray.map((data, index) => {
            return (
              <Card
                raised={false}
                // class={selected.has(data.id) ? "contained" : "outlined"}
                sx={{
                  width: { xs: "100%", sm: "60%" },
                  mb: index + 1 === mindTrapsArray.length ? 7 : 2,
                  mr: 2,
                  backgroundColor: selected.has(data.id)
                    ? theme.palette.primary.main
                    : theme.palette.customWhite.main,

                  color: selected.has(data.id)
                    ? theme.palette.customWhite.main
                    : theme.palette.primary.main,
                }}
                id={`custom-card-${data.id}`}
              >
                <CardActionArea
                  onClick={() => {
                    handleSelectionChanged(data.id);
                  }}
                >
                  <CardContent>
                    <Typography
                      gutterBottom
                      variant="h6"
                      component="div"
                      sx={{ fontWeight: 600, fontSize: "0.9rem" }}
                    >
                      {data.name}
                    </Typography>
                    <Typography variant="body2" sx={{ fontSize: "0.7rem" }}>
                      {data.desc}
                    </Typography>
                  </CardContent>
                </CardActionArea>
              </Card>
            );
          })}
        </Grid>
      </Box>
    </>
  );
}

export default MindTraps;
